export default {
    props: {
        placeholder: {
            type: String,
            default: '请选择'
        },
        items: {
            type: Array,
            default: [{
                label: 'label1',
                value: ''
            }, {
                label: 'label2',
                value: ''
            }, {
                label: 'label3',
                value: ''
            }, ]
        }
    },
    data() {
        return { 
            current: 0
        }
    },
    methods: {
        clickItem(index) {
            this.current = index;
            this.$emit('change', this.items[index])
        },
        radioChange: function(evt) {
            for (let i = 0; i < this.items.length; i++) {
                if (this.items[i].value === evt.target.value) {
                    this.current = i;
                    break;
                }
            }
            this.$emit('change', this.items[this.current]);
        }
    },
}
